function dateCellRender(value: Moment) {
  const timestamp = +value;
  const start = moment('2022-07-15').valueOf();
  if (timestamp < start || value.weekday() !== 4) return;

  const index = Math.floor((timestamp - start) / weekTime);

  return plans[index]?.map(function (s) {
    if (typeof s === 'object') {
      return <div><a href={s.href} target="_blank" rel="noreferrer">{s.content}</a></div>
    }

    return <div>{s}</div>
  });
}

export default function App() {
    return (
        <div className="App">
          <Calendar locale={locale} dateCellRender={dateCellRender} />
        </div>
    );
}
